<template>
    <div class="mall-order">
      <div class="tab-con">
        <div v-for="tab in tabList" :key="tab.status" @click="setTabStatus(tab.status)" :class="tabIndex===tab.status?'tab-active':''" class="tab-item">
          {{tab.name}}
          <span v-show="tabIndex===tab.status" class="bottom-line"></span>
        </div>
      </div>
      <div class="order-con">
        <MyLoadMore :list='orderList'  @is-loadmore = 'loadmore' :isLoad='isLoad'>
        <div class="order-item" v-for="order in orderList" :key="order.id">
          <div class="order-info"  @click="toOrderDetail(order.status,order.id)">
            <img :src="order.goods_img[0]" alt="">
            <div class="order-detail">
              <span class="order-status" :style="styleList[order.status-1]">
                <i v-if="order.status==1">待兑换</i>
                <i v-if="order.status==2">待发货</i>
                <i v-if="order.status==3">待收货</i>
                <i v-if="order.status==4">已完成</i>
                <i v-if="order.status==5">已取消</i>
              </span>
              <p class="order-title">{{order.goods_name }}</p>
              <div>使用积分:{{order.goods_fee }} </div>
              <div>数量:{{order.nums }}</div>
            </div>
          </div>
          <div class="order-option">
            <mt-button class="btn sure-btn" type="primary" v-if="order.status<=1" @click="changeStatus(0,order.id,order.goods_fee)">确认兑换</mt-button>
            <mt-button class="btn cancel-btn" v-if="order.status<=2"  @click="abandon(order.id)" type="default">取消兑换</mt-button>
          </div>
        </div>
        </MyLoadMore>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
import Vue from "Vue";
import Button from 'mint-ui/lib/button';
import 'mint-ui/lib/button/style.css';
import  MyLoadMore  from '@/common/MyLoadMore'
import { apiPost, apiGet } from "../../common/js/api.js"
import { Toast } from "vant";
Vue.component(Button.name, Button);
import{ Dialog } from "vant";

  const tabList = [
    {name:"全部",status:0},
    {name:"待兑换",status:1},
    {name:"待发货",status:2},
    {name:"待收货",status:3}
  ]
export default {
    name: "mall_order_list",
  components:{ MyLoadMore },
    data(){
      return{
        tabList,
        isLoad:true,
        tabIndex:0,
        page:1,
        styleList:{
          "0":{'color':'#589CE1'},//待兑换
          "1":{'color':'#0BB3A5'},//待发货
          "2":{'color':'#E1A558'},//待收货
          "3":{'color':'#C14C4C'},//已完成
          "4":{'color':'#BCBCBC'},//已取消
        },
        orderList:[

        ]
      }
    },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      this.accessToken=this.$cookie.get('accessToken');
      // this.accessToken='k-qLlWedkLlQ6rh3jY5lzeDzVXImT8U4';

      this.getMallOrderList();
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //点击切换订单列表
      setTabStatus(status){
        this.tabIndex = status;
        this.page=1;
        console.log(this.tabIndex)
        this.getMallOrderList()
      },
      //跳转到订单详情
      toOrderDetail(status,orderId){
        if(status==1){
          return
        }
        this.$router.push({
          path:"/orderDetail",
          query:{
            orderId
          }
        })
      },
      loadmore(){
        this.isLoad = true;
        let formdata = new FormData();
        formdata.append('accessToken',this.accessToken);
        formdata.append('status',this.tabIndex)
        formdata.append('page',++this.page);
        formdata.append('size',5)
        apiPost("/order/order-list", formdata).then(res => {
          if (res.data.status === '200') {
            console.log(res.data)
            console.log(res.data.data.list,res.data.data.list.length );
            this.orderList = this.orderList.concat(res.data.data.list);

            if(res.data.data.list.length < 10) {
              this.isLoad = false;
            } else {
              this.isLoad = true
            }
          }
        })

      },
      //获取商城订单列表
      getMallOrderList(){
        let formdata = new FormData();
        formdata.append('accessToken',this.accessToken);
        formdata.append('status',this.tabIndex);
        formdata.append('page',this.page);
        formdata.append('size',5);
        console.log(formdata);
        apiPost("/order/order-list",formdata).then(res=>{
          console.log(res)
          this.orderList=res.data.data.list;
          console.log(this.orderList)
          // this.isLoad = false;
        }).catch(()=>{
          // this.isLoad = false;
        })
      },
      //确认取消兑换
      changeStatus(flag,id,goods_fee ){
       //flag==0表示确认兑换
       //flag==1表示取消兑换
       Dialog.confirm({
          title: '提示',
          message: '确认兑换该商品？'
        }).then(() => {
          // on confirm
         this.$router.push({
           path:"/sPay",
           query:{
             orderId:id,
             goods_fee:goods_fee
           }
         })
        }).catch(() => {
          // on cancel
        });
        
      },
      abandon(id){
        Dialog.confirm({
          title: '提示',
          message: '确认放弃兑换该商品？'
        }).then(() => {
          // on confirm
          let formdata = new FormData();
          formdata.append('accessToken',this.accessToken);
          formdata.append('order_id',id)
          apiPost('/order/un-order',formdata).then((res)=>{
            Toast(res.data.message);
            this.getMallOrderList();
          })
        }).catch(() => {
          // on cancel

        });
      }
    }
}
</script>

<style scoped lang="scss">
  .mall-order{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .tab-con{
    display: flex;
    background: #fff;
    width: 100%;
    height: .47rem;
    font-size: .19rem;
    color: #BCBCBC;
    .tab-item{
      flex: 1;
      text-align: center;
      line-height: .47rem;
      position: relative;
    }
    .tab-active{
      color: #3C7FEE;
    }
    .bottom-line{
      display: inline-block;
      background: #3C7FEE;
      width: 40%;
      height: .02rem;
      position: absolute;
      bottom:0;
      left:30%;
    }
  }
  .btn{
    width:.83rem;
    height: .31rem;
    line-height: .31rem;
    font-size: .15rem;
    padding:0;
  }
  .order-con{
    width: 100%;
    overflow-y: auto;
    flex: 1;
    padding: .17rem .13rem;
    overflow-x: hidden;
    .order-item{
      width:100%;
      height: 1.66rem;
      background: #fff;
      margin-bottom: .17rem;
      padding: .21rem .04rem 0 .04rem;
      border-radius: .04rem;
      display: flex;
      flex-direction: column;
      position: relative;
      .order-info{
        flex:1;
        flex-direction: row;
        display: flex;
        padding: 0 .09rem;
        &>img{
          width:.73rem;
          height: .73rem;
          margin-right: .13rem;
        }
        .order-detail{
          flex:1;
          overflow: hidden;
          .order-title{
            text-overflow: ellipsis;
            white-space: nowrap; 
            overflow: hidden;
            width: 80%;
          }
          .order-status{
           position: absolute;
           right:.13rem;
           top:.2rem;
           color:#589CE1;
           font-size: .13rem;
           i{
             font-style: normal;
           }
          }
          .order-title{
            color:#333333;
            font-size: .17rem;
          }
          &>div{
            color:#9A9A9A;
            font-size: .13rem;
            margin: .04rem 0;
          }
        }
      }
      .order-option{
        height: .5rem;
        border-top: .01rem solid #F5F5F5;
        align-items: center;
        justify-content: flex-end;
        display: flex;
        padding-right: .09rem;
      }
      .sure-btn{
        margin-right: .13rem;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
        border-radius:.04rem;
      }
      .cancel-btn{
        border:.01rem solid rgba(60,127,238,1);
        border-radius:.04rem;
        color: #3C7FEE;
        background: #fff;
      }
    }
  }
</style>



